/* React */
import React, { useState, useContext } from 'react';

/* Formik */
import { useFormik } from 'formik';

/* Material-UI */
import { useTheme, makeStyles } from '@material-ui/core/';
import Paper from '@material-ui/core/Paper';
import FormGroup from '@material-ui/core/FormGroup';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import TextField from '@material-ui/core/TextField';
import FormHelperText from '@material-ui/core/FormHelperText';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';

/* Moment */
import moment from 'moment';

/* Custom Components */
import { GridRow, GridColumn } from '@components/Grid';

/* Context */
import { ResizeContext } from '@src/App';


/* Styles Hook */
const useStyles = makeStyles((theme)=>({
  root: {
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  datetime: {
    width: "100%"
  },
  center: {
    textAlign: "center"
  }
}));


/* Sub Component */
const DateTimeField = props => {
  /* props */
  const {
    handleChange,
    ...rest
  } = props;

  /* Rendering */
  return (
    <FormControl fullWidth>
      {/* <FormLabel component="legend">{ label }</FormLabel> */}
      <TextField
        type="datetime-local"
        InputLabelProps={{
          shrink: true,
        }}
        InputProps={{
          onChange: handleChange
        }}
        { ...rest }
      />
      <FormHelperText>Required</FormHelperText>
    </FormControl>
  );
}

/* Sub Component */
const IntervalField = props => {
  /* props */
  const {
    classes,
    TypeProps: {
      name: typeName,
      label: typeLabel,
      required: typeRequired,
      ...otherTypeLabel
    },
    ValueProps: {
      required: valueRequired,
      ...otherValueProps
    },
    handleChange,
    ...rest
  } = props;

  /* Rendering */
  return (
    <GridRow>
      <GridColumn xs={ 6 }>
        <FormControl fullWidth>
          <InputLabel id={ typeName+"-label" }>{ typeLabel }</InputLabel>
          <Select
            className={ classes.center }
            labelId={ typeName+"-label" }
            name={ typeName }
            handleChange={ handleChange }
            { ...otherTypeLabel }
          >
            <MenuItem value="hours">시간</MenuItem>
            <MenuItem value="minutes">분</MenuItem>
            <MenuItem value="seconds">초</MenuItem>
          </Select>
          { typeRequired &&  <FormHelperText>Required</FormHelperText> }
        </FormControl>
      </GridColumn>
      <GridColumn xs={ 6 }>
        <FormControl>
          <TextField
            type="number"
            classes={{
              input: classes.center
            }}
            InputLabelProps={{
              shrink: true,
            }}
            InputProps={{
              onChange: handleChange,
              classes: {
                input: classes.center
              }
            }}
            { ...otherValueProps }
          />
          { valueRequired &&  <FormHelperText>Required</FormHelperText> }
        </FormControl>
      </GridColumn>
    </GridRow>
  );
}

/* Sub Component */
const CrontabField = props => {
  /* props */
  const {
    handleChange,
    required,
    ...rest
  } = props;

  /* Rendering */
  return (
    <FormControl fullWidth>
      <TextField
        type="text"
        InputLabelProps={{
          shrink: true,
        }}
        InputProps={{
          onChange: handleChange
        }}
        { ...rest }
      />
      { required &&  <FormHelperText>Required</FormHelperText> }
    </FormControl>
  );
}


/* Main Component */
const LottoShcedule = props =>{
  /* Props */
  const {
    className,
    ...rest
  } = props;

  /* Material-UI Hook */
  const classes = useStyles();
  const theme = useTheme();

  /* Context */
  const { desktop } = useContext(ResizeContext);

  /* State */
  const [ initValue, setInitValue ] = useState({
    scheduleType: "date",
    scheduleName: "",
    datetime: moment().format("YYYY-MM-DDThh:mm"),
    intervalType: "minutes",
    intervalValue: "10",
    crontab: "",
  });

  /* Formik */
  const formik = useFormik({
    initialValues: initValue,
    onSubmit(values) {
      // This will run when the form is submitted
    }
  });

  /* Rendering */
  return (
    <form noValidate>
      <Paper className={ classes.root }>
        <GridRow>
          <GridColumn xs={ desktop ? 3 : 12 } padding={ theme.spacing(2) }>
            <FormControl fullWidth required>
              <InputLabel id="schedule-type-label">Schedule Type</InputLabel>
              <Select
                labelId="schedule-type-label"
                id="scheduleType"
                name="scheduleType"
                value={ formik.values.scheduleType }
                onChange={ formik.handleChange }
              >
                <MenuItem value="date">Date</MenuItem>
                <MenuItem value="interval">Interval</MenuItem>
                <MenuItem value="crontab">CronTab</MenuItem>
              </Select>
              <FormHelperText>Required</FormHelperText>
            </FormControl>
          </GridColumn>
        </GridRow>
        <GridRow>
          <GridColumn xs={ desktop ? 3 : 12 } padding={ theme.spacing(2) }>
            {
              formik.values.scheduleType == "crontab"
              ? <CrontabField
                  label="crontab"
                  id="crontab"
                  name="crontab"
                  value={ formik.values.crontab }
                  handleChange={ formik.handleChange }
                />
              : formik.values.scheduleType == "interval"
              ? <IntervalField
                  TypeProps={{
                    label: "intervalType",
                    id: "intervalType",
                    name: "intervalType",
                    value: formik.values.intervalType,
                  }}
                  ValueProps={{
                    label: "intervalValue",
                    id: "intervalValue",
                    name: "intervalValue",
                    value: formik.values.intervalValue,
                  }}
                  classes={ classes }
                  handleChange={ formik.handleChange }
                />
              : <DateTimeField
                  label="datetime"
                  id="datetime"
                  name="datetime"
                  value={ formik.values.datetime }
                  handleChange={ formik.handleChange }
                />
            }
          </GridColumn>
        </GridRow>
      </Paper>
    </form>
  );
}

export default LottoShcedule;

/*
 * https://material-ui.com/components/selects/
 * https://material-ui.com/api/input/#css
 * https://material-ui.com/components/text-fields/
 */